export default {
  name: 'SysConfigTabs',
  props: {
    activeIndex: {
      type: String,
      default: 'status'
    },
    isCollapse: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    toggleCollapse() {
      this.$emit('update:isCollapse', !this.isCollapse);
    },
    checkMobile() {
      const isMobile = window.innerWidth <= 768; // 768px
      this.$emit('update:isCollapse', isMobile);
    },
    handleSelect(index) {
      this.$emit('update:activeIndex', index);
    }
  },
  mounted() {
    this.checkMobile();
    window.addEventListener('resize', this.checkMobile);
  },
  beforeUnmount() {
    window.removeEventListener('resize', this.checkMobile);
  }
};