import "core-js/modules/web.url-search-params.delete.js";
import "core-js/modules/web.url-search-params.has.js";
import "core-js/modules/web.url-search-params.size.js";
import fetchWithAuth from '@/utils/fetchWithAuth';
import packageInfo from '../../package.json';
export default {
  name: 'SysCogStatus',
  data() {
    return {
      loading: false,
      rebuilding: false,
      backing: false,
      restoring: false,
      indexInfo: {},
      version: packageInfo.version // package.json
    };
  },
  mounted() {
    this.fetchIndexInfo();
  },
  methods: {
    // 
    async fetchIndexInfo() {
      this.loading = true;
      try {
        const response = await fetchWithAuth('/api/manage/list?action=info', {
          method: 'GET'
        });
        if (response.ok) {
          const data = await response.json();
          this.indexInfo = data;
        } else {
          throw new Error('API');
        }
      } catch (error) {
        console.error(':', error);
        this.$message.error('');
      } finally {
        this.loading = false;
      }
    },
    // 
    async rebuildIndex() {
      this.rebuilding = true;
      try {
        const response = await fetchWithAuth('/api/manage/list?action=rebuild', {
          method: 'GET'
        });
        if (response.ok) {
          this.$message.success('');
          // 
          setTimeout(() => {
            this.fetchIndexInfo();
          }, 3000);
        } else {
          throw new Error('API');
        }
      } catch (error) {
        console.error(':', error);
        this.$message.error('');
      } finally {
        this.rebuilding = false;
      }
    },
    // 
    async backupData() {
      this.backing = true;
      try {
        const response = await fetchWithAuth('/api/manage/sysConfig/backup?action=backup', {
          method: 'GET'
        });
        if (response.ok) {
          // 
          const blob = await response.blob();
          const url = window.URL.createObjectURL(blob);
          const a = document.createElement('a');
          a.href = url;
          a.download = `imgbed_backup_${new Date().toISOString().split('T')[0]}.json`;
          document.body.appendChild(a);
          a.click();
          document.body.removeChild(a);
          window.URL.revokeObjectURL(url);
          this.$message.success('');
        } else {
          const errorData = await response.json();
          throw new Error(errorData.error || 'API');
        }
      } catch (error) {
        console.error(':', error);
        this.$message.error(': ' + error.message);
      } finally {
        this.backing = false;
      }
    },
    // 
    selectRestoreFile() {
      if (this.restoring) return;
      this.$refs.fileInput.click();
    },
    // 
    async handleFileSelect(event) {
      const file = event.target.files[0];
      if (!file) return;
      if (!file.name.endsWith('.json')) {
        this.$message.error('JSON');
        return;
      }

      // 
      try {
        await this.$confirm('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        });
        await this.restoreData(file);
      } catch (error) {
        if (error !== 'cancel') {
          console.error(':', error);
        }
      }

      // 
      event.target.value = '';
    },
    // 
    async restoreData(file) {
      this.restoring = true;
      try {
        const response = await fetchWithAuth('/api/manage/sysConfig/backup?action=restore', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json'
          },
          body: await file.text()
        });
        if (response.ok) {
          const result = await response.json();
          this.$message.success(` ${result.stats.restoredFiles}  ${result.stats.restoredSettings} `);
          // 
          setTimeout(() => {
            this.fetchIndexInfo();
          }, 1000);
        } else {
          const errorData = await response.json();
          throw new Error(errorData.error || 'API');
        }
      } catch (error) {
        console.error(':', error);
        this.$message.error(': ' + error.message);
      } finally {
        this.restoring = false;
      }
    },
    // 
    formatTime(timestamp) {
      if (!timestamp) return '';
      const date = new Date(timestamp);
      return date.toLocaleString('zh-CN', {
        year: 'numeric',
        month: '2-digit',
        day: '2-digit',
        hour: '2-digit',
        minute: '2-digit'
      });
    },
    // 
    getPercentage(value, total) {
      if (!total || total === 0) return 0;
      return Math.round(value / total * 100);
    },
    // 
    getTimeAgo(timestamp) {
      if (!timestamp) return '';
      const now = Date.now();
      const diff = now - timestamp;
      const minutes = Math.floor(diff / 60000);
      const hours = Math.floor(diff / 3600000);
      const days = Math.floor(diff / 86400000);
      if (days > 0) return `${days}`;
      if (hours > 0) return `${hours}`;
      if (minutes > 0) return `${minutes}`;
      return '';
    }
  }
};